
.app {
  display: -ms-flexbox;
  display: flex;
}
.main {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  /*flex-grow: 1;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #f1e1c8;
  color: #483e23;
}
.search {
  padding: 10px;
  margin-right: 5px;
}
.search input{
  width:100%;
  height: 40px;
  font-size: 24px;
  color: #999;
  padding-left: 5px;
}
.map{
  -ms-flex-positive: 1;
      flex-grow: 1;
  /*flex-basis: 50%;  */
  height: 100vh;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  /*background-color: mistyrose;*/
}
.offers {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}
.nav{
  /*flex-grow: 1;*/
  /*flex-basis: 5%;*/
  
  width: 1px;
  opacity: 0;  
  -webkit-transition: 1s;  
  -o-transition: 1s;  
  transition: 1s;
  /*display: none;*/
  /*visibility: hidden;*/

  /*to make sidebar stick*/
  height: 100vh;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
}

.nav:hover {
  width: 180px;
  opacity: 1;  
  /*display: block;*/
  /*visibility: visible;  */
}


